import React, {Component} from 'react';
import "./index.css"
import Mark from "./mark";
import {Route,Redirect} from "react-router-dom";

class ShowText extends Component {

    render() {

        return (
            <div id="showtext" className="h-full w-full box-border px-8 pt-12 md:pl-4  md:fixed md:top-0 md:right-0 md:w-4/5">
                <Route path="/about_me" component={Mark}></Route>
                <Route path="/array_method" component={Mark}></Route>
                <Route path="/scope" component={Mark}></Route>
                <Route path="/closure" component={Mark}></Route>
                <Route path="/enumeration" component={Mark}></Route>
                <Route path="/prototype" component={Mark}></Route>
                <Route path="/even_loop" component={Mark}></Route>
                <Route path="/promise" component={Mark}></Route>
                <Route path="/react_init" component={Mark}></Route>
                <Redirect to="/about_me"></Redirect>
            </div>
        );
    }
}

export default ShowText;