import React, {Component} from 'react';
import Lis from "../menulist2/lis";
import Pubsub from "pubsub-js";
class Menulist2 extends Component {
    state={
        dropdownstate:false,
        menu:[  {path:"about_me",lis:[]},
                {path:"javascript",lis:[{title:"数组方法",path:"array_method"},{title:"枚举 遍历",path:"enumeration"},{title:"作用域",path:"scope"},{title:"闭包",path:"closure"},{title:"原型、原型链和类",path:"prototype"},{title:"事件循环",path:"even_loop"},,{title:"Promise和async",path:"promise"}]},
                {path:"react",lis:[{title:"react基础与初始化脚手架",path:"react_init"},{title:"路由基础",path:"route_base"}]}
        ]
    }
    handleclick=(event,link_obj)=>{
        // console.log(event.target)
        if(link_obj.lis.length>0){
            // console.log(this.state.dropdownstate)
            this.setState({dropdownstate:!this.state.dropdownstate})
        }
    }
    sl= React.createRef()
    render() {
        const title=this.props.title
        const path=this.props.path
        const link_obj=this.state.menu.find((obj)=>{
            return obj.path===path
        })
        console.log(link_obj)
        const len=link_obj.lis.length
        return (
            <div className="my-4">
                <div className="flex no-underline text-base text-black" onClick={(event)=>{this.handleclick(event,link_obj)}}>
                    <span className="flex-none font-semibold pointer-events-none">{title}</span>
                    <div className="flex-1 pointer-events-none"></div>
                    {len ? this.state.dropdownstate ?
                        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2}
                             stroke="currentColor" className="w-6 h-6 pointer-events-none">
                            <path strokeLinecap="round" strokeLinejoin="round" d="m19.5 8.25-7.5 7.5-7.5-7.5"/>
                        </svg> :
                        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2}
                             stroke="currentColor" className="w-6 h-6 pointer-events-none">
                            <path strokeLinecap="round" strokeLinejoin="round" d="m8.25 4.5 7.5 7.5-7.5 7.5"/>
                        </svg> :""}
                </div>
                <div ref={this.sl}>
                    {
                        this.state.dropdownstate ? <Lis menu={link_obj.lis} path={link_obj.path}></Lis> : ""
                    }
                </div>
            </div>
        );
    }
}

export default Menulist2;