import React, {Component} from 'react';

class Search extends Component {

    divbox=React.createRef()
    inputbox=React.createRef()
    handledivclick=()=>{
        this.inputbox.current.focus()
    }
    handlefocus=()=>{
        console.log("聚焦input")
        this.divbox.current.classList.remove("ring-1","ring-gray-300")
        this.divbox.current.classList.add("ring-2","ring-blue-700")
    }
    handleblur=()=>{
        console.log("失去聚焦input")
        this.divbox.current.classList.remove("ring-2","ring-blue-700")
        this.divbox.current.classList.add("ring-1","ring-gray-300")
    }
    render() {

        return (
            <div tabIndex="-1" ref={this.divbox} className="flex my-4 px-2 py-0.5 ring-1 ring-inset ring-gray-300 border-gray-300 rounded-md shadow-sm h-10 w-full  box-border  bg-white" onFocus={this.handledivclick}>
                {/*如果要使用content-center对其容器中的项目，必须先flex-wrap允许换行*/}
                <div className="flex-none flex flex-row justify-items-center items-center">
                    <div className="h-6 w-6 flex flex-wrap justify-items-center items-center">
                        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"
                             stroke="#99a1b3" className="w-6 h-6 stroke-2">
                            <path strokeLinecap="round" strokeLinejoin="round"
                                  d="m21 21-5.197-5.197m0 0A7.5 7.5 0 1 0 5.196 5.196a7.5 7.5 0 0 0 10.607 10.607Z"/>
                        </svg>
                    </div>
                </div>
                <div className="flex-1 h-full box-border">
                    <input type="text" ref={this.inputbox} onFocus={this.handlefocus} onBlur={this.handleblur} className="appearance-none text-gray-700 border-none py-2 px-2 box-border text-base font-medium block w-full h-full focus:outline-none placeholder:opacity-0 lg:placeholder:opacity-100" placeholder="搜索..."/>
                </div>
                <div className="flex-none h-full flex items-center">
                    <span className="text-base font-bold text-slate-400 opacity-0 lg:opacity-100">Ctrl K</span>
                </div>
            </div>
        );
    }
}

export default Search;