import React, {Component} from 'react';
import {NavLink} from "react-router-dom"
import Pubsub from "pubsub-js";

class Lis extends Component {
    handleclick=()=>{
        if(window.innerWidth<768){
            Pubsub.publish("show",false)
        }
    }
    render() {
        const menu=this.props.menu
        console.log("渲染lis")
        return (
            <ul className="list-none pl-4">
                {menu.map((obj)=>{
                    return  <li key={obj.path}>
                                <NavLink to={"/"+obj.path} activeClassName={"text-sky-600 border-sky-600"} className="my-1.5 block pl-2 flex-1 no-underline text-black border-l border-slate-300 hover:border-slate-600 focus:border-sky-600 focus:text-sky-600" onClick={this.handleclick}>
                                    {obj.title}
                                </NavLink>
                            </li>
                })}
            </ul>
        );
    }
}

export default Lis;