import React, {Component} from 'react';
import MenuList2 from "./menulist2";
import Search from "./search";
import "./index.css"
import Pubsub from 'pubsub-js'
class NavAside extends Component {
    componentDidMount() {
        Pubsub.subscribe("show",(_,data)=>{
            if(data){
                document.getElementById("navaside").classList.remove("hidden")
            }
            else{
                document.getElementById("navaside").classList.add("hidden")
            }
        })
    }

    render() {
        return (
            <div id="navaside" className="h-screen w-52 box-border bg-white pt-12 px-6 fixed top-0 bottom-0 left-0 snap-y z-40 hidden md:block md:w-1/5">
                <Search></Search>
                <MenuList2 title="关于" path="about_me"></MenuList2>
                <MenuList2 title="Javascript" path="javascript"></MenuList2>
                <MenuList2 title="React" path="react" ></MenuList2>
                {/*<MenuList2 title="新手入门"></MenuList2>*/}
                {/*<MenuList2 title="新手入门"></MenuList2>*/}
                {/*<MenuList2 title="新手入门"></MenuList2>*/}
            </div>
        );
    }
}

export default NavAside;